import React, { Suspense, lazy, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

const CalenderPage: React.FC = () => {
    const { date } = useParams<{ date: string }>();  // URL   
    const [TilComponent, setTilComponent] = useState<React.LazyExoticComponent<React.ComponentType<any>> | null>(null);

    useEffect(() => {
        if (date) {
            const loadComponent = async () => {
                try {
                    //     
                    const component = await import(`../static/til/til_${date}.tsx`);
                    setTilComponent(() => component.default);  //     
                } catch (error) {
                    console.error(`TIL    : ${date}`, error);
                    setTilComponent(null);  //   null 
                }
            };

            loadComponent();  //   
        }
    }, [date]);  //     

    return (
        <div>
            <h1>TIL for {date}</h1>
            <Suspense fallback={<p> ...</p>}>
                {TilComponent ? (
                    <TilComponent />  //   TIL  
                ) : (
                    <p>  TIL    .</p>  //     
                )}
            </Suspense>
        </div>
    );
};

export default CalenderPage;
